<template>
  <div class="header">
    <div class="header-left">
      <img alt="Vue logo" src="./assets/logo.png" class="logo" />
      <span class="header-title">{{ title }}</span>
    </div>
    <div class="header-right">
      <div class="nav">
        <router-link
          v-for="item in list"
          v-show="item.name"
          :key="item.path"
          :class="['nav-item']"
          :to="item.path"
        >
          {{ item.name }}
        </router-link>
      </div>
    </div>
  </div>

  <div class="body">
    <div class="source-link">
      <a
        v-for="item in links"
        :key="item.path"
        :href="item.path"
        class="home-link"
        target="_blank"
      >
        {{ item.name }}
      </a>
    </div>
    <div class="body-title">{{ ViewTitle }}</div>

    <router-view />
  </div>

  <div class="home-footer">
    <a
      class="home-link home-link-version"
      target="_blank"
      v-bind:href="getUrl('blob/master/README.md')"
      >{{ getVersion }}</a
    >
  </div>
</template>

<script>
import pakageJson from "../package.json";
import { routes } from "@/router/index.js";

export default {
  name: "App",
  components: {},
  setup() {},
  data: function () {
    return {
      title: "Vue.js",
      list: routes,
      links: [
        {
          path: "https://v3.vuejs.org/api/application-api.html",
          name: "application-api",
        },
        {
          path: "https://www.tutorialspoint.com/vuejs/index.htm",
          name: "document",
        },
        {
          path: this.getUrl(),
          name: "source",
        },
      ],
    };
  },
  methods: {
    getUrl: (path = "") => {
      return `https://github.com/bonnv79/vuejs-hello-world/${path}`;
    },
  },
  computed: {
    getVersion: function () {
      const version =
        pakageJson && pakageJson.version ? pakageJson.version : "1.0.0";
      return `v${version}`;
    },
    ViewTitle() {
      const { name } = this.$route || {};
      return name;
    },
  },
};
</script>

